document.addEventListener(`click`, function(e) {

  if (!e.target.closest(`a`)) return

  if (!e.target.closest(`.smooth-scroll`)) return

  e.preventDefault()

  const offsetTop = document.querySelector(`${e.target.dataset.elem}`).offsetTop

  window.scroll({
    top: offsetTop,
    left: 0,
    behavior: `smooth`
  })
})