const inputs = form.querySelectorAll('.contact__input')
const btn = form.querySelector('.contact__submit')

form.addEventListener(`submit`, (e) => {

  e.preventDefault() 

  handleSubmit(form.action, form.method, new FormData(form))
})

function handleSubmit(url, method, body) {

  fetch(url, {
    method: method,
    body: body,
    body: JSON.stringify(Object.fromEntries(body)),
    headers: {
      'Content-type': 'application/json; charset=UTF-8',
    },
  }).then(response => {

    console.log(response)

    if (response.status >= 200 && response.status < 300) {

      form.reset()

      showSuccess(inputs, btn)
    }
  })
}

function showSuccess(inputs, btn) {

  inputs.forEach(input => input.classList.add(`success`))

  btn.classList.add(`success`)
}

document.addEventListener(`input`, function(e) {

  if (!e.target.closest(`.contact__input`)) return

  inputs.forEach(input => input.classList.remove(`success`))

  btn.classList.remove(`success`)
})
